import React, { useState } from "react";
import "./style.css";
import { toast } from "react-toastify";
import axios from "axios";
import MuiPhoneNumber from "material-ui-phone-number";

export const Registrationform = () => {
  const [nameis, setNameIs] = useState("");
  const [phonenumber, setPhonenumber] = useState("");
  const [emailId, setEmailId] = useState("");
  const [address, setAddress] = useState("");
  const [otp, setOtp] = useState("");
  const [isPhoneVerified, setIsPhoneVerified] = useState(false);
  const [isRequestedOtp, setIsRequestedOtp] = useState(false);

  const sendOtp = async (channel) => {
    try {

      if (phonenumber === "")
      return toast.error("please enter your phone number");

    // if (otp === "")
    //   return toast.error("please enter your otp");

      const response = await axios.post("/api/otp/requestotp", {
        // Template for sending data from F->B
        phonenumber,
        channel,
      });
      console.log(response.data);
      if (response.data.success === true) {
        setIsRequestedOtp(true);
        toast.success(`Otp sent on ${channel}`);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const verifyOtp = async () => {
    try {
      if (phonenumber === "")
        return toast.error("please enter your phone number");

      if (otp === "")
        return toast.error("please enter your otp");

      const response = await axios.post("/api/otp/verifyotp", {
        // Template for sending data from F->B
        phonenumber,
        otp,
      });
      // if (response.data.success === true) {
      //   setIsPhoneVerified(true);
      //   toast.success(`Your OTP verified ${otp}`);
      // }
      if (response.data.status === "approved") {
        toast.success("OTP Verified Successfully", {
          position: "bottom-right",
        });
        setIsPhoneVerified(true);
      } else if (response.data.status === "pending") {
        toast.error("Invalid OTP", { position: "bottom-right" });
      } else {
        toast.error(response.data.error, { position: "bottom-right" });
      }
    } catch (error) {
      console.log(error);
    }
  };

  const submitdetails = async() => {

    try {
      if (nameis.trim() === "")
      return toast.error("please enter your name");
      if (phonenumber === "")
      return toast.error("please enter your phonenumber");
      if (emailId.trim() === "")
      return toast.error("please enter your emailId");
      if (address.trim() === "")
      return toast.error("please enter your address");
      if (isPhoneVerified === false) {
        return toast.warning("Please verify your phone number first");
      } else {
        // toast.success("your details submitted");
        const response =  await axios.post("/api/savedata", {
         nameis,
         address,
         emailId,
         phonenumber
        });
        if (response.data.success) {
          toast.success("Submitted details")
        }
        else
          toast.error(response.data.error)
        
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div>
      <form className="formcontainer">
        <div className="mb-3">
          <label className="form-label">Name</label>
          <input
            type="textarea"
            className="form-control"
            aria-describedby="your full name"
            value={nameis}
            onChange={(e) => setNameIs(e.target.value)}
          />
        </div>
        <div className="mb-3">
          <label for="exampleInputEmail1" className="form-label">
            Email address
          </label>
          <input
            type="email"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            value={emailId}
            onChange={(e) => setEmailId(e.target.value)}
          />
          {/* <div id="emailHelp" className="form-text">
            We'll never share your email with anyone else.
          </div> */}
        </div>
        <div className="mb-3">
          <label className="form-label">Address</label>
          <input
            type="textarea"
            className="form-control"
            aria-describedby="your address"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Phone Number</label>
          {/* <input
            type="number"
            className="form-control"
            aria-describedby="mobile number"
            value={phonenumber}
            onChange={(e) => setPhonenumber(e.target.value)}
          /> */}
          <MuiPhoneNumber
            name="phonenumber"
            variant="outlined"
            fullWidth
            data-cy="user-phone"
            defaultCountry={"in"}
            value={phonenumber}
            required
            onChange={(e) => setPhonenumber(e)}
          />
        </div>
        {isRequestedOtp === true ? (
          <>
            <div className="mb-3">
              <label className="form-label">
                {" "}
                Enter One Time Password(OTP) sent to your number.
              </label>
              <input
                type="number"
                className="form-control"
                aria-describedby="mobile number"
                value={otp}
                onChange={(e) => setOtp(e.target.value)}
              />
            </div>
            <div className="mb-3">
            <button
              type="button"
              className="btn btn-primary mx-2"
              id="buttons"
              onClick={() => verifyOtp()}
            >
              Verify OTP
            </button>
           </div> 
          </>
        ) : (
          <>
          <div className="mb-3">
            <button
              type="button"
              className="btn btn-primary mx-2"
              id="buttons"
              onClick={() => sendOtp("sms")}
            >
              Send OTP on message
            </button>
            </div>
            <div className="mb-3">
          
            <button
              type="button"
              className="btn btn-primary mx-2"
              id="buttons"
              onClick={() => sendOtp("call")}
            >
              Send OTP on call
            </button>
            </div>
          </>
        )}
        <div className="mb-3">
        <button
          onClick={() => submitdetails()}
          type="button"
          className="btn btn-primary"
        >
          Submit
        </button>
        </div>
      </form>
    </div>
  );
};
