import axios from "axios";
import "./style.css";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
// import DeleteIcon from "@mui/icons-material/Delete";
import { MdDelete } from "react-icons/md";

// import MuiPhoneNumber from "material-ui-phone-number";

// import { toast } from "react-toastify";

export const Contactdetails = () => {
  const colours = ["#A1727B", "rgb(132, 210, 210)"];

  const [users, setUsers] = useState([]);

  const getData = async () => {
    try {
      const response = await axios.get("/api/gettingotpdata");
      if (response.data.success) setUsers(response.data.data);
    } catch (error) {
      if (error.response) {
        toast.error(error.response.data.error);
      }
    }
  };
  const handlesubmit = async (id) => {
    try {
      const response = await axios.delete(`/api/deleteData/${id}`);
      if (response.data.success) {
        getData();
        toast.success("Deleted successfully.", {
          position: "top-right",
        });
      }
    } catch (error) {
      console.log(error);
      alert("something went wrong...");
    }
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="row">
      {users.map((v, i) => {
        return (
          <>
            <div
              className="card col-md-4 mx-3 my-3"
              id="contactCard"
              style={{
                // padding:"10px",

                width: "18rem",
                backgroundColor: colours[i % colours.length],
              }}
            >
              <h5 className="card-header">{v.nameis}</h5>
              <div className="card-body">
                <h6 className="card-title">Contact Number: {v.phonenumber}</h6>
                <p>Address : {v.address}</p>
                <h6 className="card-text">Email ID: {v.emailId}</h6>

                <h6 className="card-text">
                  Date: {new Date(v.createdAt).toString()}
                </h6>
                <button
                  onClick={() => handlesubmit(v._id)}
                  type="button"
                  class="btn mt-2 btn-primary"
                  style={{
                    fontSize: "2px",
                    backgroundColor: " rgb(134, 7, 53)",
                    border: "transparent",
                  }}
                >
                  <MdDelete
                    style={{
                      fontSize: "18px",
                      backgroundColor: " rgb(134, 7, 53)",
                    }}
                  />
                </button>
              </div>
            </div>

            <br />
          </>
        );
      })}
    </div>
  );
};
